


import React from "react";
import { Tab, Image, List, FormGroup } from 'semantic-ui-react'
import {  Feed } from 'semantic-ui-react'

import Select from 'react-select';
import { colourOptions } from './data';
// reactstrap components
import {
  Card,
  Container,
  Alert,
  Jumbotron,
  Input,
  Label,
  Form,
  Button
} from "reactstrap";
import Header from "components/Headers/Header.jsx";
import axios from "axios";


class Tables extends React.Component {
  constructor() {
    super();
    this.state = {
      new_description: "",
      new_announcement: "",
      group: {
        description: "",
        members: [],
        _id: "",
        title: "",
        announcements: [{}]
      },
      current_user: {
        groups: [{ groupID: "", memberType: "", _id: "" }]
      },
    }
  }

  componentWillMount() {
    let paths = this.props.location.pathname.split("/")
    let group_id = paths[paths.length - 1]
    axios.get(`/api/groups/${group_id}`,{
      headers: {
        Authorization: localStorage.getItem('token')
      }
    })
      .then(res => {
        if (res.status === 200) {
          const data = res.data
          this.setState({ group: data.group });
          // console.log(this.state.group)
          // console.log(this.state.group.announcements)
        } else {
          console.log("Unable to get this group")
        }
        axios.get(`/api/users?name=${this.props.name}`, {
          headers: {
            Authorization: localStorage.getItem('token')
          }
        })
          .then(res => {
            if (res.status === 200) {
              const user = res.data.users[0]
              this.setState({ current_user: user });
            } else {
              console.log("Unable to get current user")
            }
          })
      })
  }

  handleInputChange = (event) => {
    const { value, name } = event.target;
    this.setState({
      [name]: value
    });
  }

  onNewAnnouncement = (event) => {
    event.preventDefault();
    let paths = this.props.location.pathname.split("/")
    let group_id = paths[paths.length - 1]
    event.preventDefault();
    axios.post(`/api/groups/${group_id}/announcements`, {
      announcement: { content: this.state.new_announcement }
    },{
      headers: {
        Authorization: localStorage.getItem('token')
      }
    })
      .then(res => {
        if (res.status === 200) {
          console.log("successfully added new announcement")
          // HARD REFRESH
          axios.get(`/api/groups/${group_id}`,{
            headers: {
              Authorization: localStorage.getItem('token')
            }
          })
            .then(res => {
              if (res.status === 200) {
                const data = res.data
                this.setState({ group: data.group });
              } else {
                console.log("Unable to get this group")
              }
            })
          // HARD REFRESH
          this.setState({ new_announcement: "" })

        } else {
          console.log("Unable to add new announcement")
        }
      })
  }

  onNewDescription = (event) => {
    event.preventDefault();
    let paths = this.props.location.pathname.split("/")
    let group_id = paths[paths.length - 1]
    event.preventDefault();
    axios.patch(`/api/groups/${group_id}/`, {
      description: this.state.new_description
    },{
      headers: {
        Authorization: localStorage.getItem('token')
      }
    })
      .then(res => {
        if (res.status === 200) {
          console.log("successfully added new description")
          // HARD REFRESH
          axios.get(`/api/groups/${group_id}`)
            .then(res => {
              if (res.status === 200) {
                const data = res.data
                this.setState({ group: data.group });
              } else {
                console.log("Unable to get this group")
              }
            })
          // HARD REFRESH
          this.setState({ new_description: "" })

        } else {
          console.log("Unable to add new description")
        }
      })
  }


  checkGroupAdmin() {
    let paths = this.props.location.pathname.split("/")
    let group_id = paths[paths.length - 1]
    let groups = this.state.current_user.groups
    for (let i = 0; i < groups.length; i++) {
      let group = groups[i];
      if (group.groupID === group_id) {
        return group.memberType === "GROUPHEAD"
      }
    }
    return false;
  }


  render() {

    let members = this.state.group.members.map((member) => {
      return (

        <List.Item>
          <Image avatar src='https://react.semantic-ui.com/images/avatar/small/christian.jpg' />
          <List.Content>
            <List.Header>{member}</List.Header>
          </List.Content>
        </List.Item>

      )
    })

    let announcement = null;
    if (this.state.group.announcements.length > 0) {
      announcement = (
        <Alert style={{ fontSize: "150%" }} color="primary">
          {this.state.group.announcements[this.state.group.announcements.length - 1].content}
        </Alert>
      )
    }

    let breakfast = null;
    if (this.state.group.announcements.length > 0) {
      breakfast = (
        <Alert style={{ fontSize: "150%" }} color="warning">
          Please vote for your favorite house breakfast next week.
        </Alert>
        
      )
    }
    let hs = null;
    if (this.state.group.announcements.length > 0) {
      hs = (
        <Alert style={{ fontSize: "150%" }} color="info">
          Recent and Upcoming House Sports Events!
        </Alert>
        
      )
    }
    const panes = [
      {
        menuItem: 'Announcements', render: () => (
          <Tab.Pane>
            {announcement}
            <Form style={(!this.checkGroupAdmin()) ? { display: 'none' } : {}} onSubmit={this.onNewAnnouncement} >
              <FormGroup >
                <Label for="new_announcement">Post an announcement</Label>
                <Input
                  name="new_announcement"
                  placeholder="sample announcement"
                  value={this.state.new_announcement}
                  onChange={this.handleInputChange}
                ></Input>
                <Button type="submit">Submit</Button>
              </FormGroup>
            </Form>
            <Jumbotron>
              <h1>House Description:</h1>
              <p className="lead">{this.state.group.description}</p>
              {/* FOR NOW TAKE OUT EDIT DESC */}
              <Form style={(!this.checkGroupAdmin()) ? { display: 'none' } : {}} onSubmit={this.onNewDescription} >
                <FormGroup >
                  <Label for="new_description">Edit an Description</Label>
                  <Input
                    name="new_description"
                    placeholder="sample description"
                    value={this.state.new_description}
                    onChange={this.handleInputChange}
                  ></Input>
                  <Button type="submit">Submit</Button>
                </FormGroup>
              </Form>
            </Jumbotron>
          </Tab.Pane>
          
        )
      },
      {
        menuItem: 'House Breakfast', render: () => (
          <Tab.Pane>
            {breakfast}
            <Select
    defaultValue={[colourOptions[2]]}
    isMulti
    name="colors"
    options={colourOptions}
    className="basic-multi-select"
    classNamePrefix="select"
  />
                <Button style={{backgroundColor:"#00FF00", color: "white",marginTop: "3px"}}>Submit</Button>
            

          </Tab.Pane>
        )
      },
      {
        menuItem: 'House Sports', render: () => (
          <Tab.Pane>
            {hs}
            <Card>
    
      <Feed>
        <Feed.Event>
          <Feed.Label image='https://react.semantic-ui.com/images/avatar/small/christian.jpg' />
          <Feed.Content>
            <Feed.Date style={{color:"blue"}} content='Tomorrow' />
            <Feed.Summary>
             4:00PM <a>Bremners</a> vs <a>Mowbrays</a>  ||| House Hockey 
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>
        <Feed.Event>
          <Feed.Label image='https://cdn.shopify.com/s/files/1/0712/4751/products/SMA-01_2000x.jpg?v=1571438902' />
          <Feed.Content>
            <Feed.Date style={{color:"blue"}} content='In 2 days' />
            <Feed.Summary>
            3:45PM <a>Bremners</a> vs <a></a>  ||| House Soccer
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>

        <Feed.Event>
          <Feed.Label image='https://react.semantic-ui.com/images/avatar/small/christian.jpg' />
          <Feed.Content>
            <Feed.Date content='3 days ago' />
            <Feed.Summary>
            2-1 <a>Bremners</a> vs <a>Orrs</a>  ||| House Hockey
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>

        <Feed.Event>
          <Feed.Label image='https://cdn.shopify.com/s/files/1/0712/4751/products/SMA-01_2000x.jpg?v=1571438902' />
          <Feed.Content>
            <Feed.Date content='4 days ago' />
            <Feed.Summary>
            1-5 <a>Bremners</a> vs <a>Scaddings</a>  ||| House Soccer
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>

       
      </Feed>
  
  </Card>

          </Tab.Pane>
        )
      },
      {
        menuItem: 'Members', render: () => <Tab.Pane>
          <List animated verticalAlign='middle'>
            {members}
          </List>

        </Tab.Pane>
      }
    ]


    return (
      <>
        <Header />
        {/* Page content */}

        <Container className="mt--7" fluid>
          {/* Table */}

          {/* <Row> */}
          <div className="col">
            <Card className="shadow">
              <div>
                <h2 style={{ marginBottom: "2%", marginTop: "1%", marginLeft: "1%" }}>{this.state.group.title}</h2>
              </div>
              <Tab panes={panes} />
            </Card>
            <Jumbotron>
       
              <p className="lead">{this.state.group.title} Head and Prefix</p>
              {/* FOR NOW TAKE OUT EDIT DESC */}
                <h1>Stefan Markarov</h1>
                <p>Aden, Awe, Filtername, Examp</p>
                  
            </Jumbotron>
          </div>



          {/* </Row> */}


        </Container>

      </>
    );
  }
}



export default Tables;
